

import React, { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'


import { withRouter, NavLink} from 'react-router-dom';

import {  OpenWatchDialog, LoadBlocks} from '../../actions/common'

import * as _Util from '../../store/Util'

import '../_styles.css'

import loadable from '@loadable/component'

const Icon2 = loadable(() => import('../Icons'))



const useObserveChanges = () => {
  const observeChanges =  useSelector(state => state.observeChanges);
  const dispatch = useDispatch(); 

  const _openMd = (_id, item) => {
    let data = {};
    data['zIndex']=450;
    data['Id']=_id;
    data['observeResize']=true;    
    data['props']={item:item}; 

    OpenWatchDialog(dispatch,data);
  }
  
  const _LoadBlocks = (q, item) => {
    LoadBlocks(q,dispatch)
  }
  


  return { 
    observeChanges,
    _openMd,
    _LoadBlocks
  }
}




  














const BlocksComponent = (props) => {
  const {
    _LoadBlocks
  } = useObserveChanges();

  let _state = _Util.getStore();
  let keys = _Util.getGlobalsKeys()
  _state["keys"] = keys;
 

  const [initialize, setInitialize] = useState(false);

  const [widthScreen, setWidthScreen] = useState(null);
   
  let searchHash = window.location.hash.split('?')[1]?window.location.hash.split('?')[1]:null;
 
  const router = _Util.parseQuery(searchHash);
  let _id = router.id?router.id:null;
 


 const typeBrowse = window.location.hash.split('/')[1];

  let outerWidth = _state["outerWidth"];


  const id  = _id || _state["ActiveUser"];

  let blockList = _state["blockList"];

  useEffect(() => {  
    if(widthScreen!==outerWidth){
      setWidthScreen(outerWidth);
    } 
    if(!initialize){
      let Qry = {
        params:{
          user:id,
          serviceAreaId:"74d8d8ff-1500-439e-acb6-5e6de1a4e81c",
          // serviceAreaId:"08a38a1c-a2b7-47b3-beb9-9f376b58e45f",
          
        },
        fields:[
          "createdAt","priceAmount","serviceAreaId","minuteHours","schedulingType","user","startTime"
        ],
        query:"getBlockByServiceArea"
      };
      _LoadBlocks(Qry);
      setInitialize(true);
      setTimeout(()=>window.scrollTo(0,0),350);
    }
  });

  

    return (
      <>
        <div className={`main2View blockList`} >        
          <div className={` _dsplFlx `}>
            <div className={`flxbsc20 pddLeft15`}>
              <NavLink  to={{pathname:"/browse"}} className="">
                <p className={`_Id_card `}>
                  <Icon2 name={'arrowBack'}/>
                </p>
              </NavLink>
            </div>  
          </div>
          <div className={`lolomo paddblock`}>
            {blockList && _Util.ObjectKeys(blockList).map(minh=>{             
              let list = _Util.ObjectKeys(blockList[minh])
              return(
                <ItemBlocksComponent minh={minh} list={list}key={_Util.gen12CodeId()} />
              )
            })}
          </div>
        </div>
      </>
    );
  
}  





export default withRouter(BlocksComponent)








